.modal-overlay {
  height: 100vh;
  width: 100vw;
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  background-color: rgba(103, 110, 131, 0.5);
}

.modal {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  row-gap: 32px;
  background-color: #EAEEF6;
  margin: 0;
  padding: 32px;
  border-radius: 24px;
  border: none;
  width: 512px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  overflow-y: auto;
  max-height: calc(100% - 32px);

  @media (max-width: 768px) {
    padding: 20px;
  }
}

.close-button {
  margin-left: auto; 
}

.close-button svg {
  color: #20222C !important;
}