.button {
  min-height: 48px;
  width: 100%;
  height: 48px;
  padding: 0 12px;
  border-radius: 08px;
  font-size: 14px;
  line-height: 21px;
  font-weight: 600;
  font-family: inherit;
  white-space: nowrap;
  border: 1px solid
    var(--UI--colors-primary400, var(--video-contracting-color-primary));

  @media (min-width: 769px) {
    min-height: 40px;
    height: 40px;
  }
}

.button.primary {
  background-color: var(--UI--colors-primary400, var(--video-contracting-color-primary));
  color: #ffffff;
}
.button.primary:hover {
  background-color: var(--UI--colors-primary500, var(--video-contracting-color-secondary));
}

.button.outline {
  background-color: transparent;
  color: var(--UI--colors-primary400, var(--video-contracting-color-primary));
}
.button.outline:hover {
  color: var(--UI--colors-primary500, var(--video-contracting-color-secondary));
  border: 1px solid var(--UI--colors-primary500, var(--video-contracting-color-secondary));
}